<template>
<div>
  <div class="home-top">
    <a-row>
      <a-col :span="8">
        <div class="home-left">
          <div class="home-title-dev">
            <div class="dmc-guide-title">
              <span>设备接入服务</span>
            </div>
            <p style="color: #282B33;">从这里开始物联世界之路</p>
            <p>物联平台为您提供各种服务实例，满足您不同的使用资源</p>
          </div>
          <div class="dev-list">
            <div class="dev-item">
              <div class="card-desc" @click="goDev('/access')">
                <span>平台接入地址</span>
                <a-icon type="arrow-right" />
              </div>
              <p class="card-description">不同协议设备/应用的接入地址</p>
            </div>
            <a-divider type="vertical" dashed style="height: 3.6rem;" />
            <div class="dev-item">
              <div class="card-desc" @click="goDev('/portal')">
                <span>新手引导</span>
                <a-icon type="arrow-right" />
              </div>
              <p class="card-description">了解设备接入典型场景操作流程</p>
            </div>
          </div>
        </div>
      </a-col>
      <a-col :span="16">
        <div class="home-right">
          <img src="../../assets/images/home/home.png" alt="">
        </div>
      </a-col>
    </a-row>
  </div>
  <div class="home-bottom">
    <p class="home-header">资源概览</p>
      <a-row :gutter="16">
        <a-col :span="8" class="hone-itembg" @click="goDev('/product')">
          <div class="home-item">
            <span>产品数</span>
            <span class="num">{{productNum}}</span>
          </div>
        </a-col>
        <a-col :span="8" class="hone-itembg" @click="goDev('/resourceAllocationList')">
          <div class="home-item">
            <span>资源规则数</span>
            <span class="num">{{resNum}}</span>
          </div>
        </a-col>
        <a-col :span="8" class="hone-itembg" @click="goDev('/dataForwardingList')">
          <div class="home-item">
            <span>数据转发规则</span>
            <span class="num">{{forwardNum}}</span>
          </div>
        </a-col>
      </a-row>
      <a-row :gutter="16" style="margin-top: 16px;">
        <a-col :span="8" class="hone-itembg" @click="goDev('/equipment')">
          <div class="home-item">
            <span>设备总数</span>
            <span class="num">{{deviceCount.TOTAL}}</span>
          </div>
        </a-col>
        <a-col :span="8" class="hone-itembg" @click="goDev('/equipment')">
          <div class="home-item">
            <span>在线设备总数</span>
            <span class="num">{{deviceCount.ONLINE}}</span>
          </div>
        </a-col>
        <a-col :span="8" class="hone-itembg" @click="goDev('/equipment')">
          <div class="home-item">
            <span>激活设备总数</span>
            <span class="num">{{deviceCount.TOTAL - deviceCount.INACTIVE}}</span>
          </div>
        </a-col>
      </a-row>
    </div>
</div>
</template>

<script>
export default {
  name: 'homeIndex',
  data() {
    return {
      deviceCount: {
        TOTAL: 0,
        INACTIVE: 0,
        ONLINE: 0
      },
      productNum: 0,
      resNum: 0,
      forwardNum: 0
    }
  },
  created() {
    this.getDeviceNumber()
  },
  methods: {
    /**
     * 获取设备数
     * */
    getDeviceNumber() {
      const obj = {
        productId: ''
      }
      this.$api.getCountDevice(obj).then(res => {
        console.log('设备数量', res)
        this.deviceCount = res.data
      })
      this.$api.getProductNum().then(res => {
        console.log('产品数量', res)
        this.productNum = res.data
      })
      this.$api.getResNum().then(res => {
        console.log('资源数量', res)
        this.resNum = res.data
      })
      this.$api.getForwardNum().then(res => {
        console.log('规则数量', res)
        this.forwardNum = res.data
      })
    },
    goDev(val) {
      this.$router.push(val)
    }
  }
}
</script>

<style lang="scss" scoped>
p {
  margin: 0;
}
.home-top {
  margin: 24px;
  border-radius: 4px;
  background: #ffffff;
  height: 19rem;
  .dmc-guide-title {
    display: inline-block;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #dfe1e6;
    padding-right: 44px;
    margin-bottom: 10px;
    span {
      font-size: 20px;
      letter-spacing: 1px;
      font-weight: 700;
    }
  }
  .home-left {
    padding: 24px;
  }
  .home-right {
    width: 100%;
    padding-left: 32px;
    padding-top: 20px;
    padding-right: 24px;
    img {
      width: 100%;
      height: 275px;
    }
  }
}
.home-bottom {
  background: #ffffff;
  padding: 24px;
  margin: 24px;
  .home-header {
    font-size: 16px;
    font-weight: 800;
    margin-bottom: 16px;
  }
  .hone-itembg {
    height: 2.5rem;
    line-height: 2.5rem;
    cursor: pointer;
    .home-item {
      background: rgba(16,103,238,0.04);
      cursor: pointer;
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      font-size: 14px;
      .num {
        font-weight: 700;
      }
      &:hover {
        background: rgba(16,103,238,0.1);
      }
      &:active {
        background: rgba(16,103,238,0.1);
      }
    }
  }
}
.dev-list {
  display: flex;
  margin-top: 1.5rem;
  .card-desc {
    color: #0456de;
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
  }
  .card-description {
    font-size: 12px;
    color: #8f929d;
    margin: 10px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>



